@import url(https://fonts.googleapis.com/css?family=Lato:100);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.styles_container__WdslF {
    background: lightblue;
    cursor: url('https://uploads.codesandbox.io/uploads/user/b3e56831-8b98-4fee-b941-0e27f39883ab/Ad1_-cursor.png') 39 39,
      auto;
  }
  .styles_deck__2J5XN {
    position: absolute;
    width: 100vw;
    height: 100vh;
    will-change: transform;
    display: flex;
    align-items: center;
    justify-content: center;
    touch-action: none;
  }
  
  .styles_deck__2J5XN > div {
    background-color: white;
    background-size: auto 85%;
    background-repeat: no-repeat;
    background-position: center center;
    width: 45vh;
    max-width: 300px;
    height: 85vh;
    max-height: 570px;
    will-change: transform;
    border-radius: 10px;
    box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
  }
  
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.homePage{
  width: 100%;
  height: 200vh;
  /* height: 1000px; */
  color: #fff;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400%, 400%;
  position: relative;
  -webkit-animation: change 10s ease-in-out infinite;
          animation: change 10s ease-in-out infinite;
  position: relative;
}
.greeting{
  width: 30%;
  height: 25%;
  position: absolute;
  /* font-size: 100px; */
  top: 15%;
  left: 3%;
  font-size: 800%;
  text-align: left;

  /* background-color: black; */
}

.subtitle{
  height: 19%;
  width: 45%;
  position: absolute;
  top: 27.5%;
  left: 3%;
  font-size: 150%;
  text-align: left;
  line-height: 130%;
  /* background-color: black; */
}

.icons{
  height: 10%;
  width: 45%;
  position: absolute;
  top: 37%;
  left: 3%;
  font-size: 250%;
  text-align: left;
  line-height: 130%;
}

.icons-email{
  cursor: pointer;
}

.icons-github, .icons-linkedin, .icons-phone{
  margin-left: 15%;
  cursor: pointer;
  /* background-color: black; */
}

.frontPage-aboutMe{
  position: absolute;
  top: 100vh;
  width: 100%;
  height: 100vh;
  /* background-color: rgb(255, 255, 255); */
  background-color: white;
}

.menu{
  position: absolute;
  right: 0;
  padding-left: 150px;
}

@-webkit-keyframes change {
  0% {
    background-position: 0 50%;
  }
  50%{
    background-position: 100% 50%;
  }
  100%{
    background-position: 0 50%;
  }
}

@keyframes change {
  0% {
    background-position: 0 50%;
  }
  50%{
    background-position: 100% 50%;
  }
  100%{
    background-position: 0 50%;
  }
}

